* {
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: #555;
  background-color: #f5f5f7;
  font-family: Lexend, sans-serif;
  font-weight: 400;
  line-height: 1.6;
}
